.GamePad {
    width: 100%;
    height: calc(100vh - 4rem);
    overflow: hidden;
}

.BrakeButton {
    position: absolute;
    bottom: 14rem;
    right: 1rem;
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
    border: none;
    background-color: palevioletred;
    color: black;
    font-weight: bold;
    box-shadow: 0 6.4px 14.4px 0 rgba(0, 0, 0, 0.132), 0 1.2px 3.6px 0 rgba(0, 0, 0, 0.108);
    user-select: none;
    touch-action: none;
    -webkit-touch-callout: none;
}

.LeftPad {
    position: absolute;
    left: 0rem;
    bottom: 0rem;
    height: 200px;
    width: 200px;
    padding: 1rem;
    user-select: none;
    touch-action: none;
    -webkit-touch-callout: none;

    .VertDragArea {
        position: relative;
        width: 100%;
        height: 100%;
        background-color: lightgray;
        border-radius: 50%;
        user-select: none;
        touch-action: none;
        -webkit-touch-callout: none;
    }
}

.RightPad {
    position: absolute;
    right: 0rem;
    bottom: 0rem;
    height: 200px;
    width: 200px;
    padding: 1rem;
    user-select: none;
    touch-action: none;
    -webkit-touch-callout: none;

    .HorizDragArea {
        position: relative;
        width: 100%;
        height: 100%;
        background-color: lightgray;
        border-radius: 50%;
        user-select: none;
        touch-action: none;
        -webkit-touch-callout: none;
    }
}

.ThumbDrag {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
    background-color: darkgray;
    pointer-events: none;
}

.ConnectedMessage {
    position: absolute;
    bottom: 0.5rem;
    left: 0;
    right: 0;
    width: 12rem;
    margin: auto;
    background-color: #141414;
    color: white;
    border-radius: 8px;
    padding: 0.25rem;
    font-weight: bold;
    text-align: center;
}

.ApiAliveMessage {
    position: absolute;
    bottom: 4rem;
    left: 0;
    right: 0;
    width: 8rem;
    margin: auto;
    border-radius: 8px;
    padding: 0.25rem;
    font-weight: bold;
    text-align: center;

    &.Validating {
        background-color: lightskyblue;
        color: black;
    }

    &.Success {
        background-color: #77dd77;
        color: black;
    }

    &.Failure {
        background-color: #ff6961;
        color: black;
    }
}
